:root{
    --transform-value: 15%;
    --swipe-value: 2%;
}
.next-shrine-button{
    position: fixed;
    bottom: 50px;
    right: -105%;
    background-color: rgba(0,0,0,.7);
    color: rgb(202, 202, 166);
    padding: 5px 20px;
    opacity: .5;
    transition: .4s ease-in all;
    border: 2px solid white;
    border-radius: 35px;  
    z-index: 13;

    &:hover{
        background-color: rgba(0,0,0,.8);
    }
    &:hover a{
        color: rgb(224, 224, 40);       
    }
        a{
            display: block;
            padding: 10px;

            p{
                display: inline-block;
                margin: 0
            }
            color: rgb(202, 202, 166);
            img{
                width: 25px;
                height: 25px;
                vertical-align: top;
                position: relative;
                -webkit-animation: moveUpDown .75s infinite ease;
                animation: moveUpDown .75s infinite ease;
            }
        }      
}
.show-next-shrine-button{
    opacity: 1;
    bottom: 50px;
    right: var(--swipe-value);
}
@-webkit-keyframes moveUpDown{
    0%{
        -webkit-transform: translateY(0%);
    }
    75%{
        -webkit-transform: translateY(var(--transform-value));
    }
    100%{
        -webkit-transform: translateY(0%);
    }
}
@keyframes moveUpDown{
    0%{
        transform: translateY(0%);
    }
    75%{
        transform: translateY(var(--transform-value));
    }
    100%{
        transform: translateY(0%);
    }
}
@media(max-width:890px){
    :root{
        --swipe-value: 0%;
    }
    .next-shrine-button{
        transform: scale(.75,.75);
    }
}